#modal-wrapper {
  overflow: hidden;
  position: absolute;
  top: 0;
  transition: transform 0.9s ease-in-out;
  z-index: 10;
  display: none; /*Ta bort i prod*/
}
#modal {
  font-family: Lato;
  text-align: center;
  height: 100vh;
  width: 100vw;
  background-color: rgb(0, 62, 72);
  color: #b5b5b5;
  display: flex;
  flex-direction: column;
  font-size: 26px;
  align-items: center;
  justify-content: center;
  letter-spacing: 2px;
}
#modal-trail {
  height: 100vh;
  width: 100vw;
  background-image: linear-gradient(
    rgb(0, 62, 72) 0%,
    rgba(0, 62, 72, 0.8) 25%,
    rgba(0, 62, 72, 0)
  );
}
#modal button {
  font-family: Lato;
  margin-top: 70px;
  width: 250px;
  height: 70px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid white;
  color: #b5b5b5;
  font-size: 20px;
  transition: background-color 0.3s;
  cursor: pointer;
}
#modal button:hover {
  outline: none;
  background-color: rgba(196, 176, 176, 0.5);
}
#modal button:active {
  outline: none;
}
#modal button:focus {
  outline: none;
}
.modal-hidden {
  transform: translate(-350px, 0);
  -webkit-transform: translate(-350px, 0); /** Safari & Chrome **/
  -o-transform: translate(-350px, 0); /** Opera **/
  -moz-transform: translate(-350px, 0); /** Firefox **/
}
.modal-remove {
  transform: translate3d(0px, -200vh, 0px);
}

@font-face {
  font-family: "Lato";
  src: local("Lato"), url(../../../fonts/roboto/Roboto-Bold.ttf) format("woff");
}
